/* Navigation */
nav {
  position: fixed;
  top:0;
  left:0;
  bottom:0;
  background:#052a58;
  width: 100%;
  height:105px;
  display: flex;
  z-index: 2000;
  overflow:hidden;
  color:#000;
  transition: all 0.5s;
}

nav a {
  color:#fff;
  display:block
}

nav .nav_wrapper {
  margin:0 auto;
  max-width: 1300px;
  display: flex;
}

nav .logo {
  margin-left: 100px;
  margin-top: 30px;
}
nav .logo  span{
  font-family: cursive;
  font-size: 25px;
}
nav ul{
  font-family: 'Times New Roman', Times, serif;
}

nav .router {
  display: flex;
  justify-content: flex-end;
  margin-top:40px;
  margin-left: 0;
  width:100%;
}

nav .router li {
  flex-direction: column;
  margin-left: 110px;
}

nav.scroll {
  height:85px;
  transition: all 0.5s;
}

nav.scroll .router {
  margin-top:42px;
  transition: all 0.5s;
}

nav.scroll .logo {
  margin-top:8px;
  transition: all 0.5s;
}

/*hover effect*/
.menu {
	position: relative;
	z-index: 10;
}
.menu_item {
	position: relative;
	display: block;
	outline: none;
	line-height: 1;
}

.menu_item_name {
	position: relative;
  display: inline-block;
  font-size: 1.1em;
}

.menu_wrap .menu_item_name {
	transition: color 0.5s;
	transition-timing-function: cubic-bezier(0.2,1,0.3,1);
}

.menu_wrap .active {
  color: #181f39;
  font-weight: bold;
}

.menu_wrap .active .menu_item_name::before {
  background: #408274;
  transform: scale3d(1,1,1);
}

.menu_wrap .menu_item_name::before {
	content: '';
	position: absolute;
	z-index: -1;
	width: 100%;
	height: 50%;
	left: 0;
	bottom: 0;
	opacity: 0.3;
	transform: scale3d(0,1,1);
	transform-origin: 0% 50%;
	transition: transform 0.5s;
	transition-timing-function: cubic-bezier(0.2,1,0.3,1);
}

.menu_wrap .menu_item .menu_item_name::before {
	background: #408274;
}

.menu_wrap .menu_item:hover .menu_item_name::before,
.menu_wrap .menu_item:focus .menu_item_name::before {
	transform: scale3d(1,1,1);
}

