/*wave*/
@keyframes wave {
  0% { transform: rotate(0deg);}
  25% { transform: rotate(90deg);}
  50% { transform: rotate(180deg);}
  100% { transform: rotate(360deg);}
}

.one {
  position: absolute;
  top: 63px;
  height: 300px;
  width: 100%;
  transform: translate(0%, -50%);
  cursor: default;
  -webkit-filter: url(#wave);
  filter: url(#wave);
}


.circle:before, .circle:after {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  height: 10px;
  width: 310px;
  background: #f35041;
}
.circle:before {
  border-radius: 40% 40% 40% 40%;
  animation: wave 5s infinite linear;
}
.circle:after {
  border-radius: 40% 40% 40% 40%;
  animation: wave 4s infinite linear;
}


.circle1:before, .circle1:after {
  content: '';
  position: absolute;
  top: 50px;
  left: 500px;
  height: 10px;
  width: 110px;
  background: #f35041;
}
.circle1:before {
  border-radius: 40% 40% 40% 40%;
  animation: wave 4s infinite linear;
}
.circle1:after {
