  padding:0px 25px 10px;
  position: relative;
}

.items .linkWrapper ul li {
  margin-bottom: 8px;
  font-size:18px;
  display: flex;
  justify-content: flex-start;
  color:#666; 
  line-height: 1.2;
}

.items .linkWrapper .desclist {
 padding:15px 0 15px 25px
}

.items .linkWrapper .desclist li:before { 
