/*responsive*/

@media screen and (max-width: 1140px) {
  .nav_wrapper, .center {
    padding:0 20px;
  }
}

@media screen and (max-width: 1000px) {
  .listWrapper {
    display:block;
    padding-bottom: 10px;
  }
  .imgContainer {
    width:100%;
  }
  .desc {
    width: 100%;
    margin-left: 0px;
  }
  .items .linkWrapper {
    display: block;
  }
  .items .linkWrapper ul {
    padding:15px 0
  }
  /* .circle, .one {
    width:200px;
    height:200px;
  }
  .circle:before, .circle:after {
    width:200px;
    height:200px;
    transition: all 0.5s;
  } */
  .intro h1 {
    font-size: 35px;
  }
  .intro p {
    line-height: 1.2;
  }
}

@media screen and (max-width: 780px) {
  .contact ul {
    width:100%; 
    float: none;
  }
  nav .nav_wrapper{
    display:block
  }
  nav.scroll {
    height:135px
  }
  nav a {
   text-align: center;
  }
  nav.scroll .router {
    justify-content:space-around;
    margin-top:15px;
  }
  nav .router li {
    margin-left: 0;
  }
  .intro h1 {
    font-size: 22px;
  }
  .intro p {
    font-size: 16px;
    padding:0
  }
  .download {
    font-size: 14px;
  }
  .items .linkWrapper ul li.listStyle {
    display: block;
  }
  .items .linkWrapper ul li.listStyle span {
    display: inline-block;
    margin-top:5px
  }
}

@media screen and (max-width: 480px) {
  .itemContainer {
    grid-template-columns: 100%;
    grid-gap: 0px;
  }
  .items {
    margin-bottom: 20px;
  }
}